import React, { useState } from "react";
import styles from "./card.module.css";

const Card = ({ Poster, Title, Type, Year, imdbID }) => {
  const [HoverContent, setST] = useState(true);
  const truthyValue = () => setST(true);
  const falseyValue = () => setST(false);

  return (
    <div className={styles.card} onMouseEnter={falseyValue} onMouseLeave={truthyValue} key={imdbID}>
      <div className={styles.imgC}>
        <img src={Poster} alt="hello"></img>
      </div>

      <div className={HoverContent ? styles.Attri : styles.cardy}>
        <p>{Title}</p>
        <p>Type: {Type}</p>
        <p> {Year} </p>
        <p> IMDB id {imdbID} </p>
      </div>
    </div>
  );
};

export default Card;
